.chat-container.col {
  overflow: hidden;
}
.chat {
  height: 95vh;
  width: 100vw;
}
.chat.row {
  margin: 0;
  padding: 0;
}

.chat__messages {
  align-items: flex-end;
  display: grid;
  flex-wrap: wrap;
  height: 80vh;
  overflow-x: hidden;
  overflow-y: scroll;
}
.chat__messages.col {
  margin: 0;
  padding: 0;
}
.chat__messages__message {
  background-color: #eee;
  border-radius: 5px;
  display: flex;
  margin: .2rem 0;
  padding: .5rem;
  flex-direction: column;
}
.chat__messages__message__up {
  align-items: center;
  border-bottom: 1px solid #ccc;
  display: flex;
  padding: .5rem 0;
  justify-content: space-between;
}
.chat__messages__message__up h6 {
  margin: 0;
}
.chat__messages__message__up small {
  color: gray;
}
.chat__messages__message__down {
  margin-top: .8rem;
  font-weight: 300;
}

.chat__users {
  height: 80vh;
}
.chat__users.col {
  margin: 0;
  padding: 0;
}

.chat__form {
  height: 15vh;
}
.chat__form form {
  height: 100%;
  display: flex;
  align-items: center;
}
.chat__form form .input-field.col {
  margin-top: 2.7rem;
  margin-bottom: 0;
}
